import { useForm } from 'react-hook-form';
import { Grid } from '@material-ui/core';
import AutoCompleteField from 'components/form/AutoCompleteField';
import PaddedContainer from 'components/PaddedContainer';
import Card from 'components/Card';
import { ConfidentialityType, ConfidentialityTypeLabels } from 'types/ConfidentialityType';
import MoneyField from 'components/form/MoneyField';
import { RecourseType, RecourseTypeLabels } from 'types/RecourseType';
import { FeeModelType, FeeModelTypeLabels } from 'types/FeeModelType';
import PercentField from 'components/form/PercentField';
import DisplayField from 'components/form/DisplayField';
import { AccountType, AccountTypeLabels } from 'types/AccountType';
import React from 'react';

interface FacilityForm {
  feeModelType?: FeeModelType;
  recourseType?: RecourseType;
  confidentialityType?: ConfidentialityType;
  limit?: number;
  establishmentFee?: number;
  minMonthlyDrawdownFee?: number;
  drawdownFeePerc?: number;
}

const defaultValues: FacilityForm = {
  confidentialityType: ConfidentialityType.Disclosed,
  recourseType: RecourseType.Recourse,
  feeModelType: FeeModelType.SplitFee,
}

const confidentialityTypeOptions = [...ConfidentialityTypeLabels.entries()].map(([confidentialityType, label]) => 
  ({ label: label, value: confidentialityType })
);

const recourseTypeOptions = [...RecourseTypeLabels.entries()].map(([recourseType, label]) => 
  ({ label: label, value: recourseType })
);

const feeModelTypeOptions = [...FeeModelTypeLabels.entries()].map(([feeModelType, label]) => 
  ({ label: label, value: feeModelType })
);

function Facility() {
  // const history = useHistory();
  const { control } = useForm<FacilityForm>({
    defaultValues,
    mode: 'onChange',
  });

  // const primaryContact = watch('PrimaryContact');
  // const secondaryContact = watch('SecondaryContact');

  return (
    <PaddedContainer>
      <Grid container spacing={2}>
        <Grid item xs={12} lg={6}>
          <Card title="Pricing">
            <Grid container spacing={2}>
            <Grid item xs={12} sm={6}>
                <DisplayField
                  label="Type"
                  value={AccountTypeLabels.get(AccountType.InvoiceFinance)!}
                />
              </Grid>
              <Grid item xs={12} sm={6}>
                <AutoCompleteField
                  name="feeModelType"
                  control={control}
                  label="Fee Model"
                  options={feeModelTypeOptions}
                />
              </Grid>
              <Grid item xs={12} sm={6}>
                <MoneyField
                  name="limit"
                  control={control}
                  label="Limit"
                />
              </Grid>
              <Grid item xs={12} sm={6}>
                <MoneyField
                  name="establishmentFee"
                  control={control}
                  label="Establishment Fee"
                />
              </Grid>
              <Grid item xs={12} sm={6}>
                <MoneyField
                  name="minMonthlyDrawdownFee"
                  control={control}
                  label="Min Monthly Drawdown Fee"
                />
              </Grid>
              <Grid item xs={12} sm={6}>
                <PercentField
                  name="drawdownFeePerc"
                  control={control}
                  label="Drawdown Fee"
                />
              </Grid>
              <Grid item xs={12} sm={6}>
                <AutoCompleteField
                  name="confidentialityType"
                  control={control}
                  label="Confidentiality"
                  options={confidentialityTypeOptions}
                />
              </Grid>
              <Grid item xs={12} sm={6}>
                <AutoCompleteField
                  name="recourseType"
                  control={control}
                  label="Recourse"
                  options={recourseTypeOptions}
                />
              </Grid>
            </Grid>
          </Card>
        </Grid>
        <Grid item xs={12} lg={6}>
          <Card title="Accounts"></Card>
        </Grid>
      </Grid>
    </PaddedContainer>
  )
}

export default Facility;
